function genProps(attrs) {
  let str = '' // name value
  for (let i = 0; i < attrs.length; i++) {
    let attr = attrs[i]
    if (attr.name === 'style') {
      // color: red; background: yellow => { color: 'red' }
      let obj = {}
      attr.value.split(';').forEach(item => {
        let [key, value] = item.split(':')
        obj[key] = value
      });
      attr.value = obj
    }

    str += `${attr.name}:${JSON.stringify(attr.value)},` // a: b, c: d,
  }
  return `{${str.slice(0, -1)}}`
}

function genChildren(ast) {
  const children = ast.children;
  if (children) {
    return children.map(child => genChild(child)).join(',')
  } else {
    return ''
  }
}

function genChild(node) {
  if (node.type === 1) { // 
    return codegen(node)
  } else {
    // ÊñáÊú¨
    let text = node.text
    if (!defaultTagRE.test(text)) {
      return `_v(${JSON.stringify(text)})`
    } else {
      // _v( _s(name) + 'hello' + _s(name))
      let tokens = []
      let match;
      defaultTagRE.lastIndex = 0 // exec ‰ΩøÁî®ÁöÑÊó∂ÂÄô Ê≠£Âàô‰∏≠Â≠òÂú® /g ÁöÑËØù‰ºöËÆ∞ÂΩï‰ΩçÁΩÆÁöÑÊâÄ‰ª•ÈúÄË¶ÅÈáçÁΩÆ
      let lastIndex = 0
      while(match = defaultTagRE.exec(text)) {
        // console.log(match, '----');
        let index = match.index
        if (index > lastIndex) {
          tokens.push(JSON.stringify(text.slice(lastIndex, index)))
        }

        tokens.push(`_s(${match[1].trim()})`)
        lastIndex = index + match[0].length
      }
      if (lastIndex < text.length) {
        tokens.push(JSON.stringify(text.slice(lastIndex)))
      }
      return `_v(${tokens.join('+')})`
    }
  }
}

function codegen(ast) {
  // console.log(ast);
  let children = genChildren(ast)
  let code = (`_c('${ast.tag}', ${
      ast.attrs.length > 0 ? genProps(ast.attrs) : 'null'
    }${
      ast.children.length ? `,${children}` : ''
    }
  )`)
  
  return code 
}